<template>
  <div class="card" style="margin: 10px">
    <div class="card-content">
      <div class="columns ml-5" style="width: 90%">
        <div class="column is-two-thirds" style="font-weight: bold">
          Currency Transactions
        </div>
      </div>

      <vue-apex-charts
        ref="barChart"
        type="bar"
        width="500"
        height="260"
        :options="chartOptions"
        :series="series"
      ></vue-apex-charts>
    </div>
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
import NetworkManager from "@/network";

export default {
  name: "CurrencyTransactionsBarChart",
  components: {
    VueApexCharts: VueApexCharts,
  },
  data() {
    return {
      series: [
        {
          name: "Amount",
          data: [],
        },
      ],
      chartOptions: {
        chart: {
          height: 500,
          type: "bar",

          toolbar: {
            show: false,
          },
        },
        plotOptions: {
          bar: {
            dataLabels: {
              position: "bottom",
            },
            colors: {
              colors: ["#027D75"],
            },
          },
        },
        xaxis: {
          currencies: [],
        },
        title: {
          text: "",
        },
      },
    };
  },

  methods: {
    loadData: function () {
      const self = this;

      NetworkManager.apiRequest(
        "api/dashboard/currencyWiseTransactionValues",
        {},
        function (e) {
          if (e.statusCode === 200) {
            self.series = [
              {
                name: "Amount",
                data: e.data.amounts,
              },
            ];
            self.$refs.barChart.updateOptions({
              xaxis: {
                currencies: e.data.currencies,
              },
            });
          } else {
            //
          }
        }
      );
    },
  },

  mounted() {
    this.loadData();
  },
};
</script>

