<template>
    <div class="card" style="margin: 10px; min-height: 370px">
      <div class="card-content">
         
        <div class="card-content" style="">
            <div class="columns "  style="width:60% ; ">
                <div class="column is-two-thirds" style="font-weight: bold; ">Transactions</div>
                
            </div>
            
        </div>

        <vue-apex-charts ref="donutChart" type="donut" height="250" :options="chartOptions" :series="chartSeries"></vue-apex-charts>
        
      </div>
    </div>
  </template>
    
    <script>
    import VueApexCharts from "vue-apexcharts";
    import NetworkManager from "@/network";
    
    export default {
  name: "TransactionsChart",
  components: { VueApexCharts },
  data() {
    return {
      chartSeries: [65,34,65,65], // Bind your transaction data here
      chartOptions: {
        chart: {
          zoom: {
            enabled: false
          },
          toolbar: {
            show: false // hide the toolbar
          }
        },

        labels: [
            "Send Money" , "Bill Payments", "Thayga" , "E-Channeling"
          ],

        dataLabels: {
          enabled: false
        },
        stroke: {
          curve: 'smooth',
          width: 2
        },
        title: {
          text: ''
        },
        grid: {
          row: {
            colors: ['#00534E', '#00534E', ],
            opacity: 0.5
          },
        },
        xaxis: {
          categories: [],
          title: {
            text: 'Months'
          }
        },
        yaxis: {
          title: {
            text: 'Number of Allowances'
          }
        },
        legend: {
          position: 'right'
        }
      }
    };
  },
    
    methods: {
    loadData: function () {
     const self = this;
      NetworkManager.apiRequest('api/dashboard/getTotalTransactionsByMonth', {}, function (e) {
        if (e.statusCode === 200) {
          self.chartSeries = e.data.transactions;
         self.chartOptions.labels = e.data.monthNames;
        } else {
          // Handle the error case here
        }
      });
    }
  },

      mounted() {
        this.loadData();
      }
    };
    </script>
    
    
  <style scoped></style>
 